import * as React from "react";
import { graphql } from "gatsby";

import { Container, alpha } from "@mui/material";
import { PaletteMode, Typography } from "@mui/material";
import CssBaseline from "@mui/material/CssBaseline";
import Box from "@mui/material/Box";
import Divider from "@mui/material/Divider";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import ToggleButton from "@mui/material/ToggleButton";
import ToggleButtonGroup from "@mui/material/ToggleButtonGroup";
import AutoAwesomeRoundedIcon from "@mui/icons-material/AutoAwesomeRounded";

import AppAppBar from "../components/AppAppBar";
import Hero from "../components/Hero";
import LogoCollection from "../components/LogoCollection";
import Highlights from "../components/Highlights";
import Pricing from "../components/Pricing";
import Features from "../components/Features";
import FAQ from "../components/FAQ";
import Footer from "../components/Footer";

import { format } from "date-fns-jalali";

export default function BlogPostTemplate({
  data, // this prop will be injected by the GraphQL query below.
}: any) {
  const { allMarkdownRemark } = data; // data.markdownRemark holds your post data
  const { edges } = allMarkdownRemark;

  const [mode, setMode] = React.useState<PaletteMode>("light");
  const defaultTheme = createTheme({ palette: { mode } });

  const toggleColorMode = () => {
    setMode((prev) => (prev === "dark" ? "light" : "dark"));
  };

  return (
    <ThemeProvider theme={defaultTheme}>
      <CssBaseline />
      <AppAppBar mode={mode} toggleColorMode={toggleColorMode} />
      {/* <Hero /> */}
      <Box sx={{ bgcolor: "background.default" }}>
        <Box
          // dir="rtl"
          component={"section"}
          id="post"
          sx={(theme) => ({
            width: "100%",
            backgroundImage:
              theme.palette.mode === "light"
                ? "linear-gradient(180deg, #CEE5FD, #FFF)"
                : `linear-gradient(#02294F, ${alpha("#090E10", 0.0)})`,
            backgroundSize: "100% 20%",
            backgroundRepeat: "no-repeat",
            fontFamily: "Vazirmatn",
          })}
        >
          <Container
            sx={{
              display: "flex",
              flexDirection: "column",
              //   alignItems: "center",
              pt: { xs: 14, sm: 20 },
              pb: { xs: 8, sm: 12 },
            }}
          >
            {edges.map((edge) => {
              const { node } = edge;
              const { frontmatter } = node;

              return (
                <Typography variant="body1">
                  {`${frontmatter.title}`}
                </Typography>
              );
            })}
            {/* <Typography variant="h1" fontFamily={"Vazirmatn"}>
              {frontmatter.category}
            </Typography> */}
            {/* <Typography variant="caption">
              {format(Date.parse(frontmatter.date), "dd MMMM yyyy")}
            </Typography>
            <Box dangerouslySetInnerHTML={{ __html: html }} /> */}
          </Container>
        </Box>
        <Divider />
        <Footer />
      </Box>
    </ThemeProvider>
  );
}

export const pageQuery = graphql`
  query ($tag: String) {
    allMarkdownRemark(
      sort: { frontmatter: { date: DESC } }
      filter: { frontmatter: { category: { eq: $tag } } }
    ) {
      edges {
        node {
          frontmatter {
            date
            slug
            title
            category
          }
        }
      }
    }
  }
`;
